@import url(https://fonts.googleapis.com/css2?family=Chewy&family=Gudea&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font-family: 'Chewy', cursive; */
/* font-family: 'Gudea', sans-serif; */


.body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.card-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin-bottom: 25px;
  padding: 10px;
  border: 2px solid #001219;
}

.black-btn {
  width: 150px;
  height: 150px;
  color: whitesmoke;
  background-color: #001219;
  font-size: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-transform: uppercase;
  cursor: pointer;
  grid-area: 3/3/4/4;
  font-family: 'Chewy', cursive;
}

.black-btn:hover {
  -webkit-filter: opacity(.7);
          filter: opacity(.7)
}

.popup-title {
  text-align: center;
  padding: 5px;
}

.game-object {
  text-align: center;
  font-weight: bold;
  padding: 15px;
  font-family: 'Gudea', sans-serif;
}

.popup-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  border-radius: 50px;
  background: #010606;
  white-space: nowrap;
  padding: 10px;
  color: #ffffff;
  font-size: 15px;
  outline: none;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.game-instructions {
  list-style: none;
  counter-reset: instructions-counter;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.game-instructions li {
  counter-increment: instructions-counter;
  display: flex;
  flex-direction: column;
  width: 100%;
  font-size: 1rem;
  margin-bottom: 0.7rem;
  text-align: left;
  padding-left: 40px;
  padding-bottom: 10px;
  font-family: 'Gudea', sans-serif;
}

.game-instructions li::before {
  content: counter(instructions-counter);
  font-family: 'Chewy', cursive;
  font-weight: bold;
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  background: rgba(255, 198, 92, 0.9);
  color: #fff;
  font-weight: 700;
  font-style: italic;
  border-radius: 50%;
  width: 50px;
  text-align: center;
  transition: all 0.2s ease-in-out;
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: auto;
    display: flex;
    justify-content: right;
    align-items: center;
    font-family: 'Chewy', cursive;
}

.popup-inner {
    display: flex;
    flex-direction: row;
    position: relative;
    padding: 32px;
    width: 100%;
    background-color: rgba(0, 95, 115, 0.9);
    border-radius: 20px;
    color: #fff;
}

.popup-inner .close-btn {
    position: fixed;
    top: 16px;
    right: 16px;
    border-radius: 50px;
    background: #010606;
    white-space: nowrap;
    padding: 5px;
    color: #ffffff;
    font-size: 25px;
    outline: none;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
