.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .95);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.game-over {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: seashell;

}

.game-over h3 {
    margin: 0;
    padding: 0;
    text-transform: uppercase;
    font-family: 'Chewy', cursive;
    font-size: 60px;
}

.game-over h4 {
    margin-top: 25px;
    text-transform: uppercase;
    font-family: 'Gudea', sans-serif;
    font-size: 40px;
}

.game-over p {
    margin: 0 auto 25px;
    font-family: 'Gudea', sans-serif;
    font-size: 30px;
}

.reset {
    cursor: pointer;
    background-color: seashell;
    font-size: 30px;
    padding: 10px 30px;
    text-transform: uppercase;
    color: black;
    font-family: 'Gudea', sans-serif;


}
